import React, { useEffect, useState } from 'react';
import { adClientId } from '../constants/constants';

import '../styles/globals.scss';
import '../styles/bootstrap.min.css';
import '../styles/_bootswatch.scss';
import '../styles/blog-layout.scss';
import '../styles/blog-list.scss';
import '../styles/recent-articles.scss';
import '../styles/landing-page.scss';

export default function App({ Component, pageProps }) {
  const [rerender, setRerender] = useState(null);

  const onLoad = () => {
    setRerender('  ');
  };

  useEffect(() => {
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.crossorigin = 'anonymous';
    script.src = 'https://kit.fontawesome.com/e84540abee.js';
    document.head.appendChild(script);
    script.addEventListener('load', onLoad);
  }, []);

  /**
     * Loading the adsense script in each page
     */
  useEffect(() => {
    const script = document.createElement('script');
    script.type = 'text/javascript';
    script.crossorigin = 'anonymous';
    script.setAttribute('data-ad-client', adClientId);
    script.async = true;
    script.src = 'https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js';
    document.head.appendChild(script);
    script.addEventListener('load', onLoad);
  }, []);

  return (
    <>
      <Component {...pageProps} />
      {rerender}
    </>
  );
}
